export * from './dbslate.types.gen';
export * from '../actions/dbslate.actions.gen';

import {Store as ReduxStore, Dispatch as ReduxDispatch} from 'redux';

import * as t from '../types';

export type Store = ReduxStore<t.State>;
export type Dispatch = ReduxDispatch<t.State>;

/*
`is` is useful for assertions, like in switch case default blocks
eg:
    switch (foo) {
      cases...
      default: is<never>(foo); // compile-time exhaustivity checking! very wow
    }
*/
export const is = <T>(a: T): T => a;



